body{
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    --webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothng:grayscale;
    background: rgb(241, 236, 236);
}

.main{
    width: 70%;
    margin: 0 auto;
    margin-top: 30px;
    padding: 0 20px;
    box-sizing: border-box;
    
}

.movie-card {
    background: #fff;
    padding: 0;
    border: none;
    border-radius: 10px;
    /* box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #f1f1f1; */
    display: flex;
    background:linear-gradient(90deg, #054e53, #b6cbd7)
}

.movie-card:not(:first-child){
    margin-top: 20px;
}

.movie-card:last-child{
    margin-bottom: 50px;
}

.movie-card .left{
    flex: 1;
    padding: 5px;
}

.movie-card .left img{
    height: 250px;
    width: 200px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.movie-card .right{
    position: relative;
    flex: 10;
}

.movie-card .title{
    padding: 10px 20px;
    font-size: 25px;
    font-weight: 600;
}

.movie-card .plot{
    margin-top: 20px;
    padding: 0 20px;
}

.movie-card .price{
    margin-top: 20px;
    padding: 0 20px;
    font-weight: 500;
    color: black;
}

.footer{
    position: absolute;
    bottom: 0;
    display: flex;
    padding: 10px 20px;
    box-sizing: border-box;
    width: 100%;
}

.footer .rating{
    margin: 0;
    font-weight: 600;
    flex: 1;
}

.footer .star{
    font-size: 20px;
    height: 20px;
}

.footer .star-dis{
    font-size: 20px;
    font-weight: 600;
    flex: 1;
    position: relative;
}

.footer .starCount{
    font-size: 20px;
    font-weight: 600;
    position: absolute;
    left: 50;
}

.footer .str-btn{
    width: 7px;
    height: 7px;
}

.footer button{
    font-size: 15px;
    padding: 7px 10px;
    text-shadow: none;
    border-radius: 3px;
    border: none;
    font-weight: 600;
}

.favourite-btn{
    background-color: #bc16dd;
    color: #fff;
    margin: 5px;
}

.cart-btn{
    background-color: #251bb1;
    color: #fff;
    margin: 5px;
}

.unfavourite-btn, .remove-cart{
    background-color: red;
    color: #fff;
    text-shadow: none;
    margin: 5px;
}